import React from 'react';
import { motion } from 'framer-motion';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { animations } from '../../constants/animations';

interface LayoutAnimationProps {
  isVisible: boolean;
}

// Styled Components
const Container = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`;

const ViewportContainer = styled(motion.div)`
  position: absolute;
  inset: 2rem;
  background-color: #f9fafb;
  border-radius: 0.5rem;
  border: 2px dashed #d1d5db;
`;

const ViewportLabel = styled.div`
  position: absolute;
  top: -1.5rem;
  left: 1rem;
  font-size: 0.875rem;
  color: #4b5563;
  font-family: monospace;
`;

const GridContainer = styled.div`
  position: absolute;
  inset: 0;
  display: grid;
  grid-template-columns: repeat(12, minmax(0, 1fr));
  gap: 1rem;
  padding: 1rem;
  pointer-events: none;
`;

const GridLine = styled.div`
  height: 100%;
  border-left: 1px solid #e5e7eb;
`;

const ElementBox = styled(motion.div)`
  position: absolute;
  background-color: #eff6ff;
  border: 2px solid #bfdbfe;
  border-radius: 0.25rem;
`;

const ElementLabel = styled.div`
  position: absolute;
  top: -2%;
  left: 5%;
  font-size: 0.75rem;
  color: #2563eb;
  font-family: monospace;
`;

const MeasurementBox = styled(motion.div)`
  position: absolute;
  right: 18%;
  top: 0;
  background-color: white;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  font-size: 0.75rem;
  font-family: monospace;
`;

const MeasurementText = styled.div`
  color: #4b5563;
`;

const MeasurementLines = styled(motion.div)`
  position: absolute;
  inset: 0;
`;

const WidthArrow = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const ArrowLine = styled.div`
  height: 1px;
  background-color: #60a5fa;
  width: 100%;
`;

const LeftArrowhead = styled.div`
  position: absolute;
  left: 0;
  width: 0.5rem;
  height: 0.5rem;
  border-left: 2px solid #60a5fa;
  border-top: 2px solid #60a5fa;
  transform: translateY(-50%) rotate(-45deg);
`;

const RightArrowhead = styled.div`
  position: absolute;
  right: 0;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 2px solid #60a5fa;
  border-top: 2px solid #60a5fa;
  transform: translateY(-50%) rotate(45deg);
`;

const HeightArrow = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  align-items: center;
`;

const VerticalArrowLine = styled.div`
  width: 1px;
  background-color: #60a5fa;
  height: 100%;
`;

const TopArrowhead = styled.div`
  position: absolute;
  top: 0;
  height: 0.5rem;
  width: 0.5rem;
  border-left: 2px solid #60a5fa;
  border-top: 2px solid #60a5fa;
  transform: translateX(-50%) rotate(-45deg);
`;

const BottomArrowhead = styled.div`
  position: absolute;
  bottom: 0;
  height: 0.5rem;
  width: 0.5rem;
  border-left: 2px solid #60a5fa;
  border-bottom: 2px solid #60a5fa;
  transform: translateX(-50%) rotate(45deg);
`;

const ReflowHint = styled(motion.div)`
  position: absolute;
  bottom: -23%;
  right: 0;
  background-color: white;
  padding: 0.5rem 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid #e5e7eb;
`;

const ReflowText = styled.div`
  color: #474747;
  font-size: 0.875rem;
`;

const ReflowSpan = styled.span`
  font-weight: 500;
`;

const LayoutAnimation: React.FC<LayoutAnimationProps> = ({ isVisible }) => {
  const { t } = useTranslation();
  const timeline = animations.steps.layout.timeline;

  // Sample element measurements with style customizations
  const measurements = [
    { id: 'header', label: 'header', width: '100%', height: '30%', top: '0%', left: '0%', measurementStyle: { right: '-3.8rem', top: '4%' } },
    { id: 'h1', label: 'h1', width: '80%', height: '20%', top: '5%', left: '10%' },
    { id: 'main', label: 'main', width: '100%', height: '60%', top: '35%', left: '0%', measurementStyle: { right: '-3.8rem', top: '4%' } },
    { id: 'p', label: 'p', width: '80%', height: '40%', top: '45%', left: '10%' },
  ];

  return (
    <Container>
      {/* Viewport Container */}
      <ViewportContainer
        initial={{ opacity: 0 }}
        animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
        transition={{ delay: timeline.viewport.start, duration: timeline.viewport.duration }}
      >
        <ViewportLabel>
          {t('animations.layout.viewport')}
        </ViewportLabel>

        {/* Grid Lines */}
        <GridContainer>
          {Array.from({ length: 12 }).map((_, i) => (
            <GridLine key={i} />
          ))}
        </GridContainer>

        {/* Elements with Measurements */}
        {measurements.map((elem, index) => (
          <React.Fragment key={elem.id}>
            {/* Element Box */}
            <ElementBox
              initial={{ opacity: 0, scale: 0.8 }}
              animate={isVisible ? { opacity: 1, scale: 1 } : { opacity: 0, scale: 0.8 }}
              transition={{ delay: timeline.elementBoxes.start + index * 0.1, duration: 0.3 }}
              style={{
                width: elem.width,
                height: elem.height,
                top: elem.top,
                left: elem.left,
              }}
            >
              {/* Element Label */}
              <ElementLabel>
                {elem.label}
              </ElementLabel>

              {/* Measurements */}
              <MeasurementBox
                initial={{ opacity: 0 }}
                animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
                transition={{ delay: timeline.calculation.start + index * 0.1 }}
                style={elem.measurementStyle}
              >
                <MeasurementText>width: {elem.width}</MeasurementText>
                <MeasurementText>height: {elem.height}</MeasurementText>
                <MeasurementText>top: {elem.top}</MeasurementText>
                <MeasurementText>left: {elem.left}</MeasurementText>
              </MeasurementBox>

              {/* Measurement Lines */}
              <MeasurementLines
                initial={{ opacity: 0 }}
                animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
                transition={{ delay: timeline.calculation.start + index * 0.1 }}
              >
                {/* Width Arrow */}
                <WidthArrow>
                  <ArrowLine />
                  <LeftArrowhead />
                  <RightArrowhead />
                </WidthArrow>

                {/* Height Arrow */}
                <HeightArrow>
                  <VerticalArrowLine />
                  <TopArrowhead />
                  <BottomArrowhead />
                </HeightArrow>
              </MeasurementLines>
            </ElementBox>
          </React.Fragment>
        ))}

        {/* Reflow Hint */}
        <ReflowHint
          initial={{ opacity: 0 }}
          animate={isVisible ? { opacity: 1 } : { opacity: 0 }}
          transition={{ delay: timeline.reflowHint.start }}
        >
          <ReflowText>
            <ReflowSpan>{t('animations.layout.reflow')}</ReflowSpan>ï¼š
            <br />
            {t('animations.layout.reflow_desc')}
          </ReflowText>
        </ReflowHint>
      </ViewportContainer>
    </Container>
  );
};

export default LayoutAnimation;
